import "./Question.scss";
import Variant from "../Variant";

const Question = ({
  currentQuestion,
  arrQuestions,
  onNextQuestion,
  addResult,
  state,
}) => {
  return (
    <div className={`question ${state}`}>
      <div className="question__name">{currentQuestion.name}</div>
      <div className="question__variants variants">
        {currentQuestion &&
          currentQuestion.answers.map((answer) => (
            <Variant
              text={answer.text}
              currentQuestion={currentQuestion}
              arrQuestions={arrQuestions}
              onNextQuestion={onNextQuestion}
              addResult={addResult}
              key={answer.id}
            />
          ))}
      </div>
    </div>
  );
};

export default Question;
