import { Redirect, Route, useHistory, useLocation } from "react-router-dom";
import { useEffect, useState } from "react";
import { Transition } from "react-transition-group";

import Start from "./Start";
import Question from "./Question";
import Results from "./Results";

const Contents = ({ questions }) => {
  const [arrQuestions, setArrQuestions] = useState(null);
  const [activeQuestion, setActiveQuestion] = useState(null);
  const [results, setResults] = useState([]);
  const [trn, setTrn] = useState(false);

  let history = useHistory();
  let location = useLocation();

  useEffect(() => {
    setArrQuestions(questions);
  }, [questions]);

  useEffect(() => {
    const questionUri = location.pathname.split("question/")[1];
    if (questions) {
      const question = questions.find((item) => item.uri === questionUri);
      setActiveQuestion(question);
    }
  }, [location.pathname, questions]);

  const addResult = (currentItem, selectedText) => {
    setTrn(!trn);
    const newItem = currentItem.answers.find((answer) => {
      if (answer.text === selectedText) {
        answer.text = selectedText;
      }
      return answer;
    });

    const newObj = { ...currentItem, selectedAnswer: newItem.text };
    setResults([...results, newObj]);
  };

  const onBackHome = () => {
    setResults([]);
    history.push("/");
  };

  return (
    <div className="quiz__body">
      <Route exact path={"/"}>
        {arrQuestions && <Start arrQuestions={arrQuestions} />}
      </Route>
      <Route exact path={"/question/:id"}>
        {activeQuestion && (
          <Transition in={trn} timeout={500}>
            {(state) => (
              <Question
                state={state}
                currentQuestion={activeQuestion}
                arrQuestions={arrQuestions}
                onNextQuestion={(questionUri) => {
                  arrQuestions.length > activeQuestion
                    ? history.push(`/question/results`)
                    : history.push(`/question/${questionUri}`);
                }}
                addResult={addResult}
              />
            )}
          </Transition>
        )}
      </Route>
      <Route exact path={"/question/results"}>
        <Results results={results} onBackHome={onBackHome} />
      </Route>

      <Redirect to="/" />
    </div>
  );
};

export default Contents;
