import "./Popup.scss";

const Popup = ({ results }) => {
  return (
    <div className="popup">
      <div className="popup__title">Your answers!</div>
      {results &&
        results.map((elem) => {
          return (
            <div className="popup__item" key={elem.id}>
              <div className="popup__name">{elem.name}</div>
              <div className="popup__answer">{elem.selectedAnswer}</div>
            </div>
          );
        })}
    </div>
  );
};

export default Popup;
