import "./Variants.scss";

const Variant = ({
  text,
  currentQuestion,
  arrQuestions,
  onNextQuestion,
  addResult,
}) => {
  const goNext = (e) => {
    const selectedAnswer = e.target.textContent;
    addResult(currentQuestion, selectedAnswer);
    let nextItem = arrQuestions[currentQuestion.id]
      ? arrQuestions[currentQuestion.id].uri
      : "results";
    onNextQuestion(nextItem);
  };

  return (
    <div className="variants__item" onClick={goNext}>
      {text}
    </div>
  );
};

export default Variant;
